.projects {
    display: flex;
    flex-direction: column;
}

.project {
    display: flex;
    flex-direction: column;
    text-align: center;
    height: 100vh;
}

.project__image > a > img {
    object-fit: contain;
    width: 50%;
}

@media screen and (max-width: 1024px) {
    .project__image > img {
        width: 50%;
    }
}

@media screen and (max-width: 414px) {
    .project__image > img {
        width: 75%;
    }
}